        overflow: hidden;
        visibility: hidden;
        z-index: 8999
    }
    .nav-holder .nav li ul.sub-menu li,
    .nav-holder .nav li ul.children li {
        float: none;
        margin-right: 0;
        background: #fff
    }
    .nav-holder .nav li ul.sub-menu li a,
    .nav-holder .nav li ul.children li a {
        color: #ffb10b;
        padding-left: 10px;
        border-right: 0;
        display: block;
        width: 180px;
        border-bottom: 1px solid #ccc
    }
    .nav-holder .nav li ul.sub-menu li a:hover,
    .nav-holder .nav li ul.sub-menu li a:focus,
    .nav-holder .nav li ul.children li a:hover,
    .nav-holder .nav li ul.children li a:focus {
        background: #f7f7f7;
        color: #ffb10b
    }
    .nav-holder .nav li ul.sub-menu li:last-child a,
    .nav-holder .nav li ul.children li:last-child a {
        border-bottom: 0
    }
    .nav-holder .nav li:hover ul {
        top: auto;
        overflow: visible;
        visibility: visible
    }
    .footer .footer-top .vat-holder {
        text-align: right
    }
    .footer .footer-top .eightcol.first {
        width: 55%
    }
    .footer .footer-middle .footer-social {
        width: 30%
    }
    .footer .footer-middle .footer-social .mobile-li {
        float: right
    }
    .footer .footer-middle .footer-social .mobile-li.second-li {
        clear: none;
        float: right
    }
    .footer .footer-middle .footer-social .mellor-logo {
        float: right;
        margin-right: 1.5em;
        margin-top: 0
    }
    .footer .footer-middle .footer-social .circle {
        margin-left: 0.5em;
        float: right
    }
    .footer .footer-middle .eightcol.first {
        width: 70%
    }
}
@media only screen and (min-width: 1240px) {
    .mobile-menu-toggle {
        display: none
    }
    .mobile-navigation {
        display: none
    }
    .nav-holder {
        display: block
    }
    .header {
        padding: 0em;
        height: 70px
    }
    .header #inner-header .nav-holder {
        margin-top: 24px
    }
    .header #inner-header .tel-holder.twocol {
        width: 13.3333333333%;
        margin-left: 0%;
        padding: 1.36em 1em
    }
    .header #inner-header .twocol #logo {
        margin-top: 20px
    }
    .inc-lead-message .skinny .slide-info {
        width: 70%
    }
}
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
only screen and (min--moz-device-pixel-ratio: 1.5),
only screen and (min-device-pixel-ratio: 1.5) {
    #logo img {
        display: none
    }
    #logo img.retina-logo {
        display: inline-block
    }
}
