.ui-widget-content{
	height: 20px;
	margin: 0 7px;
	font-size: 20px;
	&:before,
	&:after{
		width: 10px;
	}
	&:before{
		border-radius: 10px 0 0 10px;
		left: -9px;
	}
	&:after{
		border-radius: 0 10px10px 0;
		right: -9px;
	}
}
.b-calculator{
	padding: 25px 0;
	&__tit{
		padding: 0 0 18px;
		font-size: 14px;
		line-height: 16px;
	}
	&__body{
		padding: 5px 20px 10px;
	}
	hr{
		margin-top: 3px;
	}
}
.b-cal-top{
	padding: 0 20px 24px;
	font-size: 12px;
	line-height: 16px;
	&__tit{
		max-width: 220px;
		padding: 0 0 10px;
		font-size: 23px;
		line-height: 26px;
	}
}
.b-calc-line{
	display: block;
	&__col{
		width: 130px;
		margin: 0 auto;
		padding: 0;
		&:first-child{
			width: auto;
			padding-bottom: 8px;
		}
	}
	h3{
		padding: 0 0 8px;
		font-size: 12px;
		line-height: 14px;
	}
	&--mil,
	&--vehi,
	&--fuel{
		.b-calc-line__col{
			padding-right: 0;
		}
	}
}
.b-range{
	&__numb{
		font-size: 14px;
		line-height: 16px;
	}
	&__bot{
		padding: 7px 0 0;
		font-size: 10px;
		line-height: 12px;
	}
}
.b-cost{
    font-size: 17px;
    &__input{
	    font-size: 17px;
	}
}
.b-calc-row{
	margin: 0;
	padding: 0;
	display: block;
	&__col{
		width: auto;
		padding: 15px 0;
	}
}
.b-total-ow{
	font-size: 12px;
	line-height: 14px;
	h3{
		padding: 0;
		font-size: 12px;
		line-height: 14px;
	}
	.icon{
		margin-bottom: 6px;
		&--strata{
			width: 76px;
			height: 33px;
		}
		&--large{
			width: 90px;
			height: 33px;
		}
	}
	&__row{
		padding: 24px 0 0;
	}
	&__col{
		width: calc(100% - 132px);
		&:first-child{
			width: 132px;
			padding-right: 10px;
		}
	}
	&__numb{
		font-size: 16px;
		line-height: 16px;
	}
}
.b-total-sav{
	font-size: 10px;
	line-height: 12px;
	h3{
		padding: 0 0 14px;
		font-size: 15px;
		line-height: 17px;
	}
	p{
		padding: 8px 0 0;
	}
	&__line{
		padding: 8px 0;
	}
	&__numb{
		font-size: 19px;
		line-height: 21px;
	}
}
.b-calc-txt{
	font-size: 12px;
	line-height: 17px;
	&__line{
		min-height: 50px;
		padding-left: 43px;
	}
	.icon{
		top: 14px;
		&--phone{
			width: 27px;
			height: 27px;
			top: 15px;
			left: 2px;
		}
		&--download{
			width: 33px;
			height: 27px;
		}
	}
}