.b-calculator{
	letter-spacing: 0;
	&__body{
		padding-left: 15px;
		padding-right: 15px;
	}
}
.b-calc-line{
	&__col{
		width: 110px;
		&:first-child{
			width: auto;
		}
	}
}
.b-total-ow{
	&__row{
		width: 100%;
		padding: 10px 0;
		align-items: flex-end;
		&-mob{
			display: block;
		}
	}
	&__col{
		width: calc(100% - 117px);
		padding-top: 0;
		&:first-child{
			width: 117px;
		}
	}
}
.b-total-sav{
	padding: 10px 20px 10px;
}
.b-calc-txt{
	padding: 14px 10px 12px 15px;
}
.b-cal-form{
	padding-left: 20px;
	padding-right: 20px;
}