@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-calculator{
	padding: 40px 0 48px;
	box-shadow: 0px -5px 5px 0px rgba(0, 0, 0, 0.1);
	background: $color-grey1;
	font-family: "fira-sans", Helvetica, Arial, sans-serif;
	font-size: 10px;
	font-weight: 400;
	line-height: 14px;
	letter-spacing: 1px;
	color: $color-body;
	position: relative;
	&__tit{
		padding: 0 0 20px;
		font-size: 18px;
		font-weight: 700;
		line-height: 20px;
		color: $color-yellow;
	}
	&__body{
		padding: 30px 70px 25px;
		background: $color-white;
		position: relative;
	}
	hr{
		width: 100%;
		height: 1px;
		margin: 20px 0;
		border: none;
		border-bottom: 5px solid $color-yellow;
		position: relative;
	}
}
.b-cal-top{
	padding: 16px 20px 54px;
	font-size: 23px;
	font-weight: 500;
	line-height: 35px;
	letter-spacing: 1px;
	text-align: center;
	&__tit{
		max-width: 580px;
		margin: 0 auto;
		padding: 0 0 20px;
		font-size: 51px;
		font-weight: 500;
		line-height: 57px;
		color: $color-yellow;
	}
}
.b-calc-line{
	padding: 25px 0;
	position: relative;
	@extend .b-flex;
	@extend .b-flex--middle;
	&__col{
		width: 250px;
		padding: 0 10px 0 20px;
		position: relative;
		&:first-child{
			width: calc(100% - 250px);
			padding: 0;
		}
	}
	h3{
		padding: 0 0 12px;
		font-size: 17px;
		font-weight: 700;
		line-height: 20px;
	}
	img{
		width: 100%;
		max-width: none;
		display: block;
		position: relative;
	}
	.beer-handle{
		display: none;
	}
	&--mil,
	&--vehi{
		.b-calc-line__col{
			padding-right: 25px;
		}
	}
	.b-cost{
		padding: 3px 0 0;
	}
	.over{
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 10;
	}
}
.b-range{
	margin-right: 135px;
	position: relative;
	&__numb{
		font-size: 22px;
		font-weight: 700;
		line-height: 29px;
		color: $color-yellow;
		position: absolute;
		top: 0;
		left: calc(100% + 24px);
	}
	&__bot{
		padding: 13px 0 0;
		font-size: 13px;
		font-weight: 700;
		line-height: 13px;
		position: relative;
		@extend .b-flex;
		@extend .b-flex--space-between;
	}
}
.b-cost{
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 1;
    &__input{
    	width: 70px;
    	display: inline-block;
		border: none;
		background: none;
		font-family: "fira-sans", Helvetica, Arial, sans-serif;
	    font-size: 28px;
	    font-style: normal;
	    font-weight: 700;
	    line-height: 1;
	    letter-spacing: 1px;
	    color: $color-body;
	    position: relative;
	}
	::placeholder{
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: $color-body;
	}
	::-webkit-input-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: $color-body;
	}
	::-moz-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: $color-body;
	}
	:-moz-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: $color-body;
	}
	:-ms-input-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: $color-body;
	}
	:focus {
	    &::-webkit-input-placeholder {
	        opacity: 0;
	        -moz-transition: opacity 0.3s ease;
	    }
	}
	:focus {
	    &::-moz-placeholder {
	        opacity: 0;
	        -moz-transition: opacity 0.3s ease;
	    }
	}
	:focus {
	    &:-moz-placeholder {
	        opacity: 0;
	        -moz-transition: opacity 0.3s ease;
	    }
	}
	:focus {
	    &:-ms-input-placeholder {
	        opacity: 0;
	        transition: opacity 0.3s ease;
	    }
	}
}
.b-calc-row{
	margin: 0 -15px;
	padding: 25px 0;
	position: relative;
	@extend .b-flex;
	&__col{
		width: 50%;
		padding: 0 15px;
	}
	.b-total-ow{
		margin-top: -4px;
	}
	.b-calc-txt{
		margin-bottom: 24px;
	}
}
.b-total-ow{
	font-size: 17px;
	font-weight: 700;
	line-height: 22px;
	position: relative;
	h3{
		padding: 0 0 22px;
		font-size: 17px;
		font-weight: 700;
		line-height: 20px;
	}
	.icon{
		margin-bottom: 15px;
		display: block;
		position: relative;
		svg{
			width: 100%;
			height: 100%;
			display: block;
			fill: $color-yellow;
			position: absolute;
			top: 0;
			left: 0;
		}
		&--strata{
			width: 61px;
			height: 26px;
		}
		&--large{
			width: 86px;
			height: 30px;
		}
	}
	&__row{
		padding: 22px 0;
		position: relative;
		@extend .b-flex;
		@extend .b-flex--bottom;
	}
	&__col{
		width: calc(100% - 117px);
		&:first-child{
			width: 117px;
			padding-right: 10px;
		}
	}
	&__numb{
		font-size: 26px;
		line-height: 29px;
	}
}
.b-total-sav{
	padding: 26px 45px 32px;
	background: $color-body;
	font-size: 12px;
	font-weight: 400;
	line-height: 14px;
	color: $color-yellow;
	position: relative;
	&:before{
		width: 87px;
		height: 80px;
		margin-top: -40px;
		display: block;
		content: '';
		background: 0 0 url(../images/svg/icon-save.svg) no-repeat;
		background-size: 100% 100%;
		position: absolute;
		top: 50%;
		right: 40px;
		z-index: 1;
	}
	h3{
		padding: 0 0 25px;
		font-size: 22px;
		font-weight: 700;
		line-height: 24px;
	}
	p{
		padding: 12px 0 0;
		letter-spacing: 0;
	}
	&__line{
		padding: 12px 0;
		position: relative;
		z-index: 2;
	}
	&__numb{
		font-size: 27px;
		font-weight: 700;
		line-height: 26px;
	}
}
.b-calc-txt{
	padding: 23px 35px 21px 25px;
	background: $color-yellow;
	font-size: 17px;
	line-height: 24px;
	color: $color-white;
	position: relative;
	&__line{
		min-height: 60px;
		padding: 8px 0 8px 60px;
		position: relative;
	}
	.icon{
		display: block;
		position: absolute;
		top: 16px;
		left: 0;
		svg{
			width: 100%;
			height: 100%;
			display: block;
			fill: $color-white;
			position: absolute;
			top: 0;
			left: 0;
		}
		&--phone{
			width: 38px;
			height: 38px;
			top: 19px;
			left: 3px;
		}
		&--download{
			width: 45px;
			height: 38px;
		}
	}
	a{
		font-weight: 700;
		color: $color-white;
		text-decoration: none;
		&:hover{
			color: $color-white;
			text-decoration: none;
		}
	}
}
.b-cal-form{
	padding: 35px 43px 33px;
	background: $color-grey2;
	position: relative;
	p{
		padding: 9px 0;
	}
	&__bot{
		padding-top: 22px;
	}
	&__input{
		width: 100%;
		height: 32px;
		padding: 5px 10px;
		display: block;
		border: none;
		background: $color-white;
		font-family: "fira-sans", Helvetica, Arial, sans-serif;
		font-size: 14px;
		font-weight: 400;
		line-height: 22px;
		color: #282828;
		position: relative;
	}
	&__btn{
		padding: 5px 20px;
		border: none;
		background: $color-yellow;
		font-family: "fira-sans", Helvetica, Arial, sans-serif;
		font-size: 16px;
		font-weight: 700;
		line-height: 22px;
		color: $color-white;
		position: relative;
		cursor: pointer;
	}
	::placeholder{
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: #282828;
	}
	::-webkit-input-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: #282828;
	}
	::-moz-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: #282828;
	}
	:-moz-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: #282828;
	}
	:-ms-input-placeholder {
	    opacity: 1;
	    transition: opacity 0.3s ease;
	    color: #282828;
	}
	:focus {
	    &::-webkit-input-placeholder {
	        opacity: 0;
	        -moz-transition: opacity 0.3s ease;
	    }
	}
	:focus {
	    &::-moz-placeholder {
	        opacity: 0;
	        -moz-transition: opacity 0.3s ease;
	    }
	}
	:focus {
	    &:-moz-placeholder {
	        opacity: 0;
	        -moz-transition: opacity 0.3s ease;
	    }
	}
	:focus {
	    &:-ms-input-placeholder {
	        opacity: 0;
	        transition: opacity 0.3s ease;
	    }
	}
}
@media (max-width: $sm) {
    @import 'sm';
}
@media (max-width: $xs) {
    @import 'xs';
}
@media (max-width: $xxs) {
    @import 'xxs';
}