.b-calculator{
	letter-spacing: 0;
	&__body{
		padding: 10px 30px;
	}
}
.b-calc-line{
	&__col{
		width: 200px;
		&:first-child{
			width: calc(100% - 200px);
		}
	}
}
.b-range{
	margin-right: 0;
	&__numb{
		top: auto;
		left: auto;
		right: 0;
		bottom: calc(100% + 9px);
	}
}
.b-calc-row{
	margin: 0 -12px;
	&__col{
		padding: 0 12px;
	}
}
.b-total-sav{
	padding: 17px 30px 20px;
	&:before{
		width: 68px;
		height: 62px;
		margin-top: -31px;
		right: 20px;
	}
}
.b-calc-txt{
	padding: 14px 20px 12px 17px;
}
.b-cal-form{
	padding: 20px 30px 20px;
}