*,
*::before,
*::after {
    box-sizing: border-box;
}
body {
	width: 100%;
    min-height: 100%;
    padding: 0;
    margin: 0;
    font-family: "fira-sans", Helvetica, Arial, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.6;
    color: #7f7f7f;
    overflow-x: hidden;
    -webkit-font-smoothing: subpixel-antialiased;
    position: relative;
}

html.menu-open,
body.menu-open {
    overflow: hidden;
    background-color: #454545
}

div,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select,
input,
textarea,
img,
dl,
dt,
dd,
table,
div,
td,
theder,
button,
blockquote,
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
meter,
main,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr {
    padding: 0;
    margin: 0;
}
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
main,
meter,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr,
address {
    display: block;
    font-style: normal;
}
*:active,
*:focus,
a,
input,
textarea,
button {
    outline: none !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
}
button,
input[type="submit"]{
    border-radius: 0;
}
strong {
    font-weight: 500
}
.cf,
.clearfix {
    &:before,
    &:after {
        content: '';
        display: block;
        overflow: hidden;
    }
    &:after {
        clear: both;
    }
}
.b-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    &--middle {
        align-items: center;
    }
    &--space-between {
        justify-content: space-between;
    }
    &--bottom {
        align-items: flex-end;
    }
    &--end,
    &--end-col {
        align-items: flex-end;
    }
    &--middle-col,
    &--bottom-col,
    &--space-between-col,
    &--end-col,
    &--top-col {
        flex-direction: column;
    }
    &--space-between-col {
        justify-content: space-between;
    }
    &--middle-col {
        justify-content: center;
    }
    &--bottom-col {
        justify-content: flex-end;
    }
    &--top-col {
        justify-content: flex-start;
    }
    &--right {
        justify-content: flex-end;
    }
    &--center {
        justify-content: center;
    }
}
// checkbox 
.jq-checkbox {
    width: 20px;
    height: 20px;
    margin: 1px 15px 0px 0px;
    display: inline-block;
    background: $color-white;
    cursor: pointer;
    font-size: 20px;
    line-height: 20px;
    position: relative;
    input{
        position: absolute;
        z-index: -1;
        margin: 0;
        padding: 0;
        opacity: 0;
    }
    &__div{
        width: 8px;
        height: 8px;
        margin: -4px 0 0 -4px;
        display: block;
        background-color: transparent;
        transition: all 0.3s ease;
        position: absolute;
        top: 50%;
        left: 50%;
    }
    &.checked {
        .jq-checkbox__div {
            background: $color-yellow;
        }
    }
}
.b-lab-checkbox{
	padding: 3px 0 3px 35px;
	display: block;
	position: relative;
	.jq-checkbox {
		margin: 0;
		position: absolute;
		top: 0;
		left: 0;
	}
}

